// Disable no-unused-vars, broken for spread args
/* eslint no-unused-vars: off */
import { contextBridge, ipcRenderer, IpcRendererEvent, nativeImage } from 'electron';
import { Titlebar, TitlebarColor } from "@incanta/custom-electron-titlebar";
import { colors } from '../renderer/src/theme/colors';
import path from 'path';
import { icon } from './icon';

export type Channels = 'ipc-example';

window.addEventListener('DOMContentLoaded', () => {
  // Title bar implementation
  new Titlebar({
    backgroundColor: TitlebarColor.fromHex(colors.alterGrey),
    containerOverflow: 'hidden',
    titleHorizontalAlignment: 'center',
    icon: `data:image/png;base64,${icon}`,
    iconSize: 16,
  });
});

const electronHandler = {
  ipcRenderer: {
    sendMessage(channel: Channels, ...args: unknown[]) {
      ipcRenderer.send(channel, ...args);
    },
    on(channel: Channels, func: (...args: unknown[]) => void) {
      const subscription = (_event: IpcRendererEvent, ...args: unknown[]) =>
        func(...args);
      ipcRenderer.on(channel, subscription);

      return () => {
        ipcRenderer.removeListener(channel, subscription);
      };
    },
    once(channel: Channels, func: (...args: unknown[]) => void) {
      ipcRenderer.once(channel, (_event, ...args) => func(...args));
    },
  },
};

contextBridge.exposeInMainWorld('electron', electronHandler);

export type ElectronHandler = typeof electronHandler;
